import React, {useState} from "react";
import { Avatar, Box, Button, Container, Grid, Typography } from "@mui/material";
import { AccountBalanceWallet, People, Settings } from "@mui/icons-material";
import { deepOrange } from '@mui/material/colors';
import Backdrop from "@mui/material/Backdrop";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";

const API_ENDPOINT = "api.mocaland.ir:88/QandShekan"

function Profile({user_id, active}) {
    const [FullName , setFullName] = React.useState("قندشکن")
    const [UserId , setUserId] = React.useState("0")
    const [UserName , setUserName] = React.useState("QandShekan")
    const [Balance , setBalance] = React.useState(0)
    const [Refs , setRefs] = React.useState(0)
    const [ActiveConfigs , setActiveConfigs] = React.useState(0)
    const [ProfilePic , setProfilePic] = React.useState(false)
    const [PopUpText , setPopUpText] = useState({"status":false});
    const [PopUpLoading, setPopUpLoading] = React.useState(false);
    function ClosePopUpText(){
        setPopUpText({"status":false});
    }
    function SendRefLink(){
        setPopUpLoading(true);
        var formdata = new FormData();
        formdata.append("action", "ResendReferalLink");
        formdata.append("user_id", user_id);
        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
            .then(response => response.text())
            .then(result => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"با موفقیت انجام شد" , "text": "لینک زیرمجموعه گیری برای شما ارسال شد ، دستورالعمل های بعدی را از ربات دنبال کنید."})
            })
            .catch(error => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"خطای ناشناخته رخ داد!" , "text": error})
                console.log('error', error)
            });
    }
    React.useEffect(() => {
        if (active) {
            setPopUpLoading(true);
            var formdata = new FormData();
            formdata.append("action", "GetUserInfo");
            formdata.append("user_id", user_id);
            var requestOptions = {
                method: 'POST',
                body: formdata,
                redirect: 'follow'
            };

            fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
                .then(response => response.text())
                .then(result => {
                    setPopUpLoading(false);
                    const res = JSON.parse(result)
                    if (res.status) {
                        setFullName(res.full_name);
                        setUserId(res.user_id);
                        setUserName(res.username);
                        setBalance(res.balance);
                        setRefs(res.refs);
                        setActiveConfigs(res.active_configs);
                        setProfilePic(res.profilePhoto);
                    }
                })
                .catch(error => {
                    setPopUpLoading(false);
                    setPopUpText({"status":true,"title":"خطای ناشناخته!" , "text":error})
                    console.log('error', error)
                });
        }
    }, [active]);
    if (active) {
        return (
            <Container maxWidth="sm">
                <Box sx={{mt: 4, mb: 2, display: "flex", flexDirection: "column", alignItems: "center"}}>
                    {ProfilePic === false ?
                        <Avatar
                            alt="عکس کاربر"
                            sx={{width: 65, height: 65, bgcolor: deepOrange[500] , "font-size":"30px"}}
                        >{FullName.charAt(0)}</Avatar>
                    :
                        <Avatar
                            alt="عکس کاربر"
                            sx={{width: 65, height: 65}}
                            src={`https://${API_ENDPOINT}/${ProfilePic}`}
                        />
                    }

                    <Typography variant="h4" component="h1" sx={{mt: 2}}>
                        {FullName}
                    </Typography>
                    <Typography variant="h6" component="p" sx={{mt: 1, color: "text.secondary"}}>
                        کد کاربری : {UserId}
                    </Typography>
                    <Typography variant="h6" component="p" sx={{mt: 1, color: "text.secondary", direction: "rtl"}}>
                        نام کاربری : {UserName}
                    </Typography>
                </Box>
                <Grid container spacing={3} sx={{direction: "rtl"}}>
                    <Grid item xs={12}>
                        <Button
                            variant="outlined"
                            startIcon={<AccountBalanceWallet/>}
                            fullWidth
                            sx={{justifyContent: "flex-start"}}
                        >
                            <span style={{"margin-right": "10px"}}>موجودی کیف پول : {Balance} تومان</span>
                        </Button>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <Button
                            variant="outlined"
                            startIcon={<People/>}
                            fullWidth
                            sx={{justifyContent: "flex-start","text-align":"right"}}
                            onClick={()=>SendRefLink()}
                        >
                            <span style={{"margin-right": "10px"}}>تعداد زیر مجموعه :
                                {Refs} نفر
                            <br/>
                                (برای دریافت لینک زیرمجموعه گیری کلیک کنید)
                            </span>
                            <span style={{"margin-right": "10px"}}></span>
                        </Button>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <Button
                            variant="outlined"
                            startIcon={<Settings/>}
                            fullWidth
                            sx={{justifyContent: "flex-start"}}
                        >
                            <span style={{"margin-right": "10px"}}>تعداد کانفیگ های فعال : {ActiveConfigs} عدد</span>
                        </Button>
                    </Grid>

                </Grid>

                <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={PopUpLoading}
                >
                    {/* CircularProgress برای نمایش یک انیمیشن بارگذاری استفاده می‌شود */}
                    <CircularProgress color="inherit" />
                </Backdrop>
                <div>
                    <Dialog open={PopUpText.status} sx={{direction:"rtl"}} onClose={()=>ClosePopUpText()}>
                        <DialogTitle>{PopUpText.title}</DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                {PopUpText.text}
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={()=>ClosePopUpText()}>بستن</Button>
                        </DialogActions>
                    </Dialog>
                </div>

            </Container>
        );
    }
    return (<></>);
}

export default Profile;
